.header {
  padding: 20px 0 0;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  margin: auto;
  z-index: 3;
}
.header__wrap {
  display: grid;
  grid-template-columns: 323px auto 188px;
  position: relative;
}
.header .logos {
  align-self: center;
  display: flex;
  align-items: flex-start;
}
.header .logos .logo_big svg {
  width: 148px;
  height: 108px;
  fill: #6fbcbc;
}
.header .logos .logo_small {
  margin-left: 6px;
  margin-top: 12px;
}
.header .logos .logo_small svg {
  width: 102px;
  height: 24px;
}
.header .nav {
  align-self: center;
  margin-left: auto;
  margin-top: -18px;
  margin-right: 25px;
}
.header .nav__item {
  color: #6fbcbc;
  font-size: 17px;
  font-family: 'futura-futuris', sans-serif;
  font-weight: 400;
  border: 2px solid #fff;
  border-radius: 100px;
  padding: 8px 15px;
  transition: 0.3s;
  background-color: transparent;
}
.header .nav__item:hover {
  color: #fff;
  background-color: #6fbcbc;
  border-color: #6fbcbc;
}
.header .nav__item:not(:first-of-type) {
  margin-left: 4px;
}
.header .phone {
  align-self: center;
  font-family: 'futura-futuris', sans-serif;
  color: #6fbcbc;
  font-weight: 700;
  font-size: 25px;
  margin-top: -18px;
}
.header .burger {
  position: absolute;
  display: none;
  right: 0;
  top: 0;
  width: 30px;
  height: 21px;
  z-index: 4;
  border-radius: 100px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #6fbcbc;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  border-radius: 100px;
  display: block;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #6fbcbc;
}
.header .burger.active span {
  background-color: #fff;
}
.header .burger.active span:nth-of-type(1) {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger.active span:nth-of-type(2) {
  opacity: 0;
}
.header .burger.active span:nth-of-type(3) {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 3px;
  background-color: #6fbcbc;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: auto;
}
.header .burger span:nth-of-type(3) {
  top: auto;
}
.header .mobile-menu {
  position: fixed;
  top: 50px;
  right: 0;
  height: calc(100vh - 50px);
  overflow: auto;
  width: 100%;
  z-index: 4;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
}
.header .mobile-menu .nav__item {
  border: none;
  color: #fff;
  padding: 8px 0;
  font-size: 17px;
}
.header .mobile-menu .nav__item:not(:first-of-type) {
  margin-left: 0;
}
.header .mobile-menu .phone {
  display: inline-block;
  color: #fff;
  margin-top: 40px;
}
