.product {
  padding: 70px 0 90px;
}
.product__wrap {
  display: grid;
  grid-template-areas: "title content" "booklet content" "btn content";
  grid-template-columns: minmax(min-content, 30%) minmax(min-content, 70%);
  column-gap: 15px;
  align-content: start;
}
.product__title {
  align-self: start;
  font-size: 50px;
  font-weight: 400;
  color: #6fbcbc;
  margin: 128px 0 30px;
  line-height: 1.1;
  grid-area: title;
}
.product__booklet {
  align-self: start;
  margin-bottom: 20px;
  grid-area: booklet;
}
.product__btn {
  grid-area: btn;
  justify-self: start;
  align-self: start;
  font-size: 17px;
  color: #6fbcbc;
  border: 3px solid #fff;
  border-radius: 100px;
  padding: 7px 15px;
  background-color: transparent;
  transition: 0.3s;
}
.product__btn:hover {
  background-color: #6fbcbc;
  color: #fff;
  border-color: #6fbcbc;
}
.product__content {
  grid-area: content;
  position: relative;
  padding-top: 59px;
  min-height: 700px;
}
.product__item {
  width: 230px;
  height: 200px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product__item.anim .hexagon {
  stroke-dashoffset: 0;
}
.product__item .hexagon {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 230px;
  height: 200px;
  stroke-dashoffset: 400%;
  stroke-dasharray: 400%;
  transition: 1s;
}
.product__item .hexagon polygon {
  fill: #0b1214;
  stroke-width: 4;
  stroke: #6fbcbc;
  transition: 0.3s;
}
.product__item .icon {
  fill: #6fbcbc;
  transition: 0.3s;
}
.product__item .name {
  font-size: 19px;
  font-weight: 400;
  color: #fff;
  position: absolute;
  bottom: 19px;
  left: 0;
  right: 0;
  margin: 0 auto;
  line-height: 0.9;
  max-width: 110px;
  text-align: center;
}
.product__item.active .hexagon polygon {
  fill: #6fbcbc;
}
.product__item.active .icon {
  fill: #fff;
}
.product__item_empty {
  opacity: 0.2;
  position: absolute;
}
.product__item_empty .hexagon polygon {
  fill: none;
}
.product__item_empty:nth-of-type(8) {
  right: 598px;
  top: -1px;
}
.product__item_empty:nth-of-type(9) {
  top: 169px;
  right: 0;
}
.product__item_empty:nth-of-type(10) {
  top: 500px;
  right: 188px;
}
